/**
 * Initialize the header block.
 * @return true if the block is present on the page, false otherwise
 */
function initBlock() {
    // TODO: add code here

    $('.header__slick').slick({
        infinite: true,
        autoplay: true,
        arrows: true,
        // dots: false,
        vertical: false ,
        slidesToShow: 1,
        slidesToScroll: 1,
        // dotsClass: 'my-dots',
        nextArrow: $('.header__prevArrow'),
        prevArrow: $('.header__nextArrow'),


    });


    return true;
}

